<style scoped lang="scss">
  @import "./livingpayment.scss";
</style>
<template>
    <div class="livingpayment">
        <div class="content">
            <div class="item" @click="payment('http://dfdemo.zhas.huichifangqiu.com')">
                <img class="icon" src="@/assets/living/life-dianfei.png" alt="">
                <p class="name">电费</p>
            </div>
            <div class="item" @click="payment('http://sfdemo.zhas.huichifangqiu.com')">
                <img class="icon" src="@/assets/living/life-waterfei.png" alt="">
                <p class="name">水费</p>
            </div>
            <div class="item" @click="payment('http://rqfdemo.zhas.huichifangqiu.com')">
                <img class="icon" src="@/assets/living/life-ranqifei.png" alt="">
                <p class="name">燃气费</p>
            </div>
            <div class="item list2" @click="payment('http://dsdemo.zhas.huichifangqiu.com')">
                <img class="icon" src="@/assets/living/life-tvfei.png" alt="">
                <p class="name">电视费</p>
            </div>
            <div class="item list2" @click="payment('http://kddemo.zhas.huichifangqiu.com')">
                <img class="icon" src="@/assets/living/life-kuandaifei.png" alt="">
                <p class="name">宽带费</p>
            </div>
            <!-- <div class="item"></div> -->
        </div>
    </div>
</template>
<script>
export default {
    data(){
        return{
            isLoading:true,
        }
    },
    created () {
    },
    methods:{
        payment (url) {
            location.href = url;
        }
    }
}
</script>



